// src/app/app/page.tsx
"use client";

import { useSession } from "next-auth/react";
import Link from "next/link";
import {
  ListBulletIcon,
  PencilIcon,
  BookOpenIcon,
  ClockIcon,
  ArrowUpCircleIcon,
} from "@heroicons/react/24/outline";
import { useTier } from "@/lib/db/useTier";

export default function AppHomePage() {
  const { data: session } = useSession();
  const tier = useTier();
  const isFree = tier === "free";

  const quickLinks = [
    {
      name: "My Tasks",
      href: "/app/tasks",
      icon: ListBulletIcon,
      description: "View all your to-dos",
    },
    {
      name: "My Notes",
      href: "/app/notes",
      icon: PencilIcon,
      description: "Capture a new idea",
    },
    {
      name: "My Diary",
      href: "/app/diary",
      icon: BookOpenIcon,
      description: "Write today's entry",
    },
    {
      name: "Focus Timer",
      href: "/app/pomodoro",
      icon: ClockIcon,
      description: "Start a focus session",
    },
  ];

  return (
    <div className="space-y-10">
      {/* Welcome Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">
          Welcome back, {session?.user?.name?.split(" ")[0] || "User"}!
        </h1>
        <p className="mt-2 text-lg text-gray-600">
          Here&apos;s your personal productivity hub.
        </p>
      </div>

      {/* Tier Status & Upgrade CTA */}
      <div
        className={`rounded-lg border shadow-sm p-6 ${
          isFree
            ? "bg-gradient-to-r from-blue-50 to-indigo-50 border-blue-200"
            : "bg-gradient-to-r from-green-50 to-emerald-50 border-green-200"
        }`}
      >
        <div className="flex flex-col sm:flex-row justify-between items-center gap-4">
          <div>
            <h3 className="text-xl font-semibold text-gray-900">
              {isFree
                ? "You are on the Free Tier"
                : `You are on the ${tier} Tier!`}
            </h3>
            <p className="mt-1 text-gray-600">
              {isFree
                ? "Unlock unlimited features by upgrading your plan."
                : "You have full access to all features."}
            </p>
          </div>

          {isFree && (
            <Link
              href="/app/pricing" // This is the "how to buy" link
              className="flex-shrink-0 w-full sm:w-auto flex items-center justify-center gap-2 px-6 py-3 bg-blue-600 text-white font-semibold rounded-lg shadow-md hover:bg-blue-700 transition-all duration-200 transform hover:-translate-y-0.5"
            >
              <ArrowUpCircleIcon className="h-5 w-5" />
              Upgrade to Pro
            </Link>
          )}
        </div>
      </div>

      {/* Quick Links Grid */}
      <div>
        <h2 className="text-xl font-semibold text-gray-900 mb-4">Your Tools</h2>
        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
          {quickLinks.map((link) => (
            <Link
              key={link.name}
              href={link.href}
              className="group relative flex flex-col p-6 bg-white rounded-lg shadow-md border border-gray-200 hover:shadow-lg hover:border-blue-500 transition-all duration-300 transform hover:-translate-y-1"
            >
              <span className="p-3 bg-gray-100 text-gray-700 rounded-lg inline-flex group-hover:bg-blue-100 group-hover:text-blue-600 transition-colors">
                <link.icon className="h-6 w-6" />
              </span>
              <h3 className="mt-4 text-lg font-semibold text-gray-900">
                {link.name}
              </h3>
              <p className="mt-1 text-sm text-gray-500">{link.description}</p>
            </Link>
          ))}
        </div>
      </div>
    </div>
  );
}
